@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
* {
  box-sizing: border-box;
}

body {
  background-color: #242333;
  color: #242333;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  font-family: "lato", sans-serif;
  margin: 0;
}

#tweet-quote {
  text-align: center;
}
.author {
	margin: 20px 0 33px 0;
}

.quote-container {
  width: 380px;
  height: 95vh;
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: beige;
  border-radius: 12px;
}

.main-image {
  width: 100%;
  height: 35%;
}
#main-image {
	  width: 380px;
	  height: 200px;
	  object-position: center top;
	  object-fit: cover;
}	
.main-quote-text {
  height: 50%;
  font-size: 1.1em;
  padding:20px;
  text-align: center;
  display: flex;
  flex-direction: column;
}

.header {
	height: 15%; 
	text-align: center;
}

.button {
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 12px;
  background-color: white;
  cursor: pointer;
}

a:link {
  padding: 10px;
  color: green;
  //border-style: solid;
  //border-color: white;
  border-radius: 12px;
  background-color: white;
  text-decoration: none;
  
}
a:visited {
  color: green;
}
a:hover {
  color: red;
}
a:active {
  color: yellow;
}

